<template>
  <div class="cal_con">
    <div class="cal_header">
      <div class="blueBtn">
        <span class="cal_h_time">{{ year }}年</span>
        <span class="cal_h_time month">
          <el-date-picker
            v-model="month"
            @change="monthChange"
            type="month"
            format="M月"
            value-format="M"
            clear-icon="none"
          >
          </el-date-picker>
        </span>
      </div>
      <span
        style="
          color: #5d89bf;
          font-size: 0.16rem;
          position: absolute;
          right: 0.24rem;
        "
        >单位：{{ unit }}</span
      >
    </div>

    <div class="cal_month">
      <div class="cal_m_weeks">
        <span v-for="w in weeks" :key="w" class="cal_m_day_cell">{{ w }}</span>
      </div>

      <div class="cal_m_days">
        <div
          v-for="(ds, index) in monthData"
          :key="index"
          class="cal_m_day_line"
        >
          <div
            v-for="d in ds"
            :key="d.day"
            :class="['cal_m_day_cell', { isCurrent: getCurretnMonthClass(d) }]"
          >
            <p>{{ d.day }}</p>
            <p v-if="calendarArry.length > 0">{{ getData(d) }}</p>
            <slot :name="d.fullYear + '-' + d.month + '-' + d.day"></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "Calendar",
  props: {
    date: {
      type: Date,
      default: () => new Date(),
    },
    arry: {
      type: Array,
      default: () => [],
    },
    unit: {
      type: String,
      default: () => "",
    },
  },
  watch:{
    arry(val){
      this.calendarArry = val
    }
  },
  data() {
    return {
      now: this.date,
      year: 0,
      month: "1",
      weeks: ["周日", "周一", "周二", "周三", "周四", "周五", "周六"],
      monthData: [],
      currentYear: new Date().getFullYear(),
      currentMonth: new Date().getMonth() + 1,
      currentDay: new Date().getDate(),
      calendarArry:[]
    };
  },
  computed: {},
  mounted() {
    this.setYearMonth(this.now);
    this.generateMonth(this.now);
  },
  methods: {
    monthChange() {
      this.$bus.$emit('canlendarValFun',this.month)
    },
    setYearMonth(now) {
      this.year = now.getFullYear();
      this.month = JSON.stringify(now.getMonth() + 1);
    },
    generateMonth(date) {
      date.setDate(1);
      // 星期 0 - 6， 星期天 - 星期6
      let weekStart = date.getDay();
      let endDate = new Date(date.getFullYear(), date.getMonth() + 1, 0);
      let dayEnd = endDate.getDate();
      // 星期 0 - 6， 星期天 - 星期6
      let weeEnd = endDate.getDay();
      let milsStart = date.getTime();
      let dayMils = 24 * 60 * 60 * 1000;

      let monthDatas = [];
      let current;
      // 上个月的几天
      for (let i = 0; i < weekStart; i++) {
        current = new Date(milsStart - (weekStart - i) * dayMils);
        monthDatas.push({
          type: -1,
          date: current,
          fullYear: current.getFullYear(),
          month: current.getMonth() + 1,
          day: current.getDate(),
        });
      }
      // 当前月
      for (let i = 0; i < dayEnd; i++) {
        current = new Date(milsStart + i * dayMils);
        monthDatas.push({
          type: 0,
          date: current,
          fullYear: current.getFullYear(),
          month: current.getMonth() + 1,
          day: current.getDate(),
        });
      }
      // 下个月的几天
      for (let i = 0; i < 6 - weeEnd; i++) {
        current = new Date(milsStart + i * dayMils);
        monthDatas.push({
          type: 1,
          date: current,
          fullYear: current.getFullYear(),
          month: current.getMonth() + 1,
          day: current.getDate(),
        });
      }

      this.monthData = [];
      for (let i = 0; i < monthDatas.length; i++) {
        let mi = i % 7;
        if (mi == 0) {
          this.monthData.push([]);
        }
        this.monthData[Math.floor(i / 7)].push(monthDatas[i]);
      }

      // 少于6行，补足6行
      if (this.monthData.length <= 5) {
        milsStart = current.getTime();
        let lastLine = [];
        for (let i = 0; i < 7; i++) {
          current = new Date(milsStart + i * dayMils);
          lastLine.push({
            type: 1,
            date: current,
            fullYear: current.getFullYear(),
            month: current.getMonth() + 1,
            day: current.getDate(),
          });
        }
        this.monthData.push(lastLine);
      }
    },

    getCellColor(d) {
      if (
        d.fullYear == this.currentYear &&
        d.month == this.currentMonth &&
        d.day == this.currentDay
      ) {
        return "#409eff";
      }
      let color = d.type == -1 ? "#c0c4cc" : d.type == 1 ? "#c0c4cc  " : "";
      return color;
    },
    getCurretnMonthClass(d) {
      if (d.type == 0) {
        return true;
      } else {
        return false;
      }
    },
    /**
     * 获取日历下对应的数据
     * **/
    getData(d){
      let num = ''
      if(d.type == 0){
        num = this.toFixedTwo(Number(this.calendarArry[d.day-1].actualUseQty))
        return num
      }
    },
    /**
     * 数据保留两位小数
     * **/
    toFixedTwo(val) {
      if (val == 0) {
        return 0;
      }
      if (val) {
        //1.先判断是不是整数，如果不是整数则保留两位小数
        let num = val;
        if (!this.$isInteger(val)) {
          num = num.toFixed(2);
        }
        return num;
      }
    },
  },
};
</script>

<style scoped lang="less">
.cal_con {
  width: 100%;
  height: 100%;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  color: #fff;
  border-radius: 4px;
  margin: auto;
  .cal_header {
    padding: 0.03rem 0.09rem;
    font-size: 0.2rem;
    margin-top: 0.12rem;
    // height: 24px;
    // padding: 5px;
    // font-size: 14px;
    // line-height: 24px;
    display: flex;
    justify-content: center;
    justify-items: center;
    position: relative;
    .cal_h_time {
      cursor: pointer;
    }
    .cal_h_time.month:hover {
      color: #409eff;
    }

    .cal_h_left {
      height: 100%;
      display: flex;

      .cal_h_btn {
        height: 100%;
        width: 24px;
        cursor: pointer;
      }

      .cal_h_btn:hover {
        background-color: #ebeef5;
      }

      .cal_h_l_icon {
        height: 8px;
        width: 12px;
        margin: auto;
      }
    }
  }

  .cal_m_days {
    .cal_m_day_cell {
      background: linear-gradient(
        to bottom,
        rgba(0, 68, 173, 0),
        rgba(7, 49, 134, 0.31) ;
      );
      border: 1px solid rgba(40, 59, 112, 1);
      margin: 0.015rem 0.035rem;
      border-radius: 0.05rem;
      p:last-child {
        color: #1a3fb3;
        font-size: 0.16rem;
      }
      p:first-child {
        color: #6e7dad;
        font-size: 0.16rem;
      }
    }
  }
  .cal_month {
    font-size: 12px;
    text-align: center;
    height: calc(100% - 34px);

    .cal_m_day_cell {
      width: calc(100% / 7);
      // cursor: pointer;
      position: relative;
      flex-grow: 1;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      overflow: hidden;
    }
    .cal_m_day_cell:hover {
      // color: #409eff;
    }

    .cal_m_weeks {
      height: 16px;
      padding: 8px;
      display: flex;
      justify-content: space-around;
      justify-items: center;
      //   border-bottom: 1px solid #e4e7ed;
    }

    .cal_m_days {
      height: calc(100% - 49px);
      padding: 8px;
      display: flex;
      justify-content: space-around;
      justify-items: center;
      flex-wrap: wrap;
      .cal_m_day_line {
        width: 100%;
        display: flex;
        justify-content: space-around;
        justify-items: center;
      }
    }
  }
}
.isCurrent {
  border-radius: 0.05rem;
  border: 1px solid rgba(34, 175, 237, 0.5) !important;
  background: linear-gradient(
    to bottom,
    rgba(3, 92, 229, 0.64),
    rgba(7, 49, 134, 0.31)
  ) !important;

  p:last-child {
    color: #00a6ff !important;
    font-size: 0.18rem;
  }
  p:first-child {
    color: #ffffff !important;
    font-size: 0.16rem;
  }
}
.blueBtn {
  font-size: 0.2rem;
  background: #0066ff;
  border-radius: 0.05rem;
  padding: 0.03rem 0.09rem;
}
.cal_h_time.month /deep/ .el-input__inner {
  padding: 0 !important;
  height: unset;
  line-height: unset;
  width: 35px;
  background-color: transparent;
  border: none;
  color: #fff;
  font-size: 0.2rem;
  cursor: pointer;
}
.cal_h_time.month /deep/ .el-input__inner:hover{
  color: #409eff;
}
.cal_h_time.month /deep/ .el-input__prefix {
  display: none;
}
.cal_h_time.month /deep/ .el-date-editor.el-input {
  width: unset;
}
</style>