import { request } from "./request";
import Vue from 'vue'

export function planExecution() {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/planExecution?areaId=" + sessionStorage.getItem('companyId'),
        method: 'POST'
    })
}
export function hourlyEnergyTrend(dateType,energyType,fromDate,toDate) {
    return request({
        url: "/web/apps/gongqi.hsb.energy/energyManageAction/hourlyEnergyTrend?dateType=" + dateType +'&energyType='+energyType+'&fromDate='+fromDate+'&id='+sessionStorage.getItem('companyId')+'&toDate='+toDate,
        method: 'POST',
        headers: { showLoading: false },
    })
}
export function comprehensive() {
    return request({
        // url: "/web/apps/gongqi.hsb.energy/energyManageAction/comprehensive?dateType=月&areaId="+sessionStorage.getItem('companyId'),
        url:null,
        method: 'POST',
        headers: { showLoading: false },
    })
}
