<template>
  <div class="statisticalEcharts">
    <div class="tabAll">
      <div>
        <div
          v-for="(item, index) in tab"
          :key="index"
          :class="{ isTabActive: tabIndex == index }"
          @click="changeIndex(index)"
        >
          {{ item }}
        </div>
      </div>
    </div>
    <div
      style="
        display: flex;
        flex-direction: column;
        position: relative;
        flex-grow: 1;
      "
    >
      <div class="eleTab">
        <div
          v-for="(item, index) in timeTab"
          :key="index"
          :class="{ isActive: index == timeIndex }"
          @click="changeTime(index)"
        >
          {{ item }}
        </div>
      </div>
      <div
        class="echart"
        style="margin-top: 0.15rem; width: 98%"
        ref="dlEchart"
      ></div>
    </div>
  </div>
</template>
<script>
import { hourlyEnergyTrend } from "@/network/comprehensiveEnergyConsumption";
export default {
  data() {
    return {
      tab: [
        "综合能耗",
        "电",
        "冷凝水",
        "自来水",
        "蒸汽",
        "天然气",
        "氮气",
        "压缩空气",
        "中水"
      ],
      tabIndex: 0,
      timeTab: ["日", "月", "年"],
      timeIndex: 0,
    };
  },
  mounted() {
    this.setEchart();
    this.hourlyEnergyTrendFun();
  },
  methods: {
    /**
     * 获取分项用能数据
     * **/
    hourlyEnergyTrendFun() {
      let fromDate = "";
      let toDate = "";
      if (this.timeIndex == 1) {
        fromDate = this.$getDate().year + "-01-01";
        toDate = this.$getDate().year + "-12-31";
      } else if (this.timeIndex == 2) {
        fromDate = Number(this.$getDate().year)-1 + "-01-01";
        toDate = this.$getDate().year + "-12-31";
      } else {
        fromDate = this.$getDate().year + "-" + this.$getDate().month + "-01";
        toDate =
          this.$getDate().year +
          "-" +
          this.$getDate().month +
          "-" +
          this.$getMonthDay(this.$getDate().year, this.$getDate().month);
      }
      this.$parent.$parent.fxLoad = false
      // hourlyEnergyTrend(
      //   this.timeTab[this.timeIndex],
      //   this.tab[this.tabIndex],
      //   fromDate,
      //   toDate
      // )
      //   .then((res) => {
      //     const { content, errcode, errmsg } = res;
      //     if (errcode == 0) {
      //       this.setEchart(content.list);
      //     }
      //     this.$parent.$parent.fxLoad = false
      //   })
      //   .catch((err) => {
      //     this.$parent.$parent.fxLoad = false
      //   });
    },
    /**
     *  切换Tab
     * **/
    changeIndex(index) {
      this.tabIndex = index;
      this.hourlyEnergyTrendFun();
    },
    /**
     *  切换年月日Tab
     * **/
    changeTime(index) {
      this.timeIndex = index;
      // this.hourlyEnergyTrendFun();
    },
    // setEchart(array)
    setEchart() {
      // let xAxisArry = [];
      // let yAxisArry = [];
      // let yAxisArry1 = [];
      // let yAxisArry2 = [];
      // arry.forEach((item) => {
      //   xAxisArry.push(item.dateStr);
      //   yAxisArry.push(item.outPutQty);
      //   yAxisArry1.push(item.actualUseQty);
      //   yAxisArry2.push(item.planUseQty);
      // });
      let chart = this.$refs.dlEchart;
      let myChart = this.$echarts.init(chart);
      myChart.clear();
      let option = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "cross",
            label: {
              backgroundColor: "#6a7985",
            },
          },
        },
        legend: {
          x: "right",
          y: "top",
          textStyle: {
            color: "#fff",
          },
          selected:{
            '实际用量':true,
            '总产量':false,
            '计划用量':false,
          }
        },
        grid: {
          left: "3%",
          right: "0%",
          bottom: "2%",
          top: "15%",
          containLabel: true,
        },
        xAxis: [
          {
            type: "category",
            boundaryGap: true,
            // data: xAxisArry,
            data: [2,6,8,5,1,4,9,6,5,4],
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            axisTick: {
              show: false,
            },
          },
        ],
        yAxis: [
          {
            type: "value",
            axisLabel: {
              show: true,
              textStyle: {
                color: "#ffffff",
              },
            },
            splitLine: {
              show: true,
              lineStyle: {
                color: ["rgba(255, 255, 255, 0.5)"],
                type: "dashed",
              },
            },
          },
        ],
        series: [
          {
            name: "总产量",
            type: "line",
            z: 2,
            emphasis: {
              focus: "series",
            },
            lineStyle: {
              normal: {
                color: "#EA00FC",
              },
            },
            itemStyle: {
              normal: {
                color: "#EA00FC",
              },
            },
            // data: yAxisArry,
            data: [4,8,10,14,3,2,8,6,5,14]
          },
          {
            name: "实际用量",
            type: "bar",
            z: 1,
            smooth: true,
            emphasis: {
              focus: "series",
            },
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "rgba(255, 226, 0, 1)" }, //柱图渐变色
                  { offset: 1, color: "rgba(255, 152, 0, 1)" }, //柱图渐变色
                ]),
              },
            },
            // data: yAxisArry1,
            data: [2,6,8,5,1,6,9,5,6,3,2]
          },
          {
            name: "计划用量",
            type: "bar",
            z: 1,
            smooth: true,
            emphasis: {
              focus: "series",
            },
            itemStyle: {
              normal: {
                color: new this.$echarts.graphic.LinearGradient(0, 0, 0, 1, [
                  { offset: 0, color: "#00e2ff" }, //柱图渐变色
                  { offset: 1, color: "#0042ff" }, //柱图渐变色
                ]),
              },
            },
            // data: yAxisArry2,
            data: [6,4,2,0.5,1.6,9,1.7,2.5,5,3.5]
          },
        ],
      };
      myChart.setOption(option);
      window.addEventListener("resize", function () {
        myChart.resize();
      });
    },
  },
};
</script>
<style scoped lang="less">
.statisticalEcharts {
  height: 100%;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}
.normalBk {
  background: rgba(0, 54, 129, 1);
  color: #ffffff;
}
.tabAll {
  display: flex;
  justify-content: space-between;
  padding: 0 0.41rem 0 0.22rem;
  padding-top: 0.12rem;
  align-items: flex-end;
  border: 1px solid rgba(34, 175, 237, 0.5);
  > div {
    display: flex;
    > div {
      cursor: pointer;
      .normalBk();
    }
  }
  > div:first-child > div {
    font-size: 0.18rem;
    padding: 0 0.32rem;
    height: 0.4rem;
    border-radius: 0.04rem 0.04rem 0 0;
    margin: 0 0.03rem;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
.isTabActive {
  background: #0056dc;
  font-size: 0.18rem;
  letter-spacing: 0.01rem;
}
.isActive {
  background: #0056dc !important;
}
.eleTab {
  display: flex;
  margin-top: 0.19rem;
  margin-left: 0.32rem;
  > div {
    cursor: pointer;
    font-size: 0.16rem;
    padding: 0.05rem 0.15rem;
    margin: 0 1px;
    .normalBk();
  }
  > div:first-child {
    border-radius: 0.04rem 0 0 0.04rem;
  }
  > div:last-child {
    border-radius: 0rem 0.04rem 0.04rem 0;
  }
}
.date {
  display: flex;
  align-items: center;
  color: #22afed;
  font-size: 0.17rem;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  top: 0.19rem;
  img {
    margin: 0 0.1rem;
    cursor: pointer;
  }
  span {
    margin: 0 0.19rem;
  }
}
.echart {
  flex-grow: 1;
}
</style>